import React, { Component } from 'react';

class Portfolio extends Component {
  render() {

    const portfolio = [
      {
        "title": "Flutter vs React Native",
        "category": "Komparasi Flutter dan React Native dalam Pengembangan Perangkat Bergerak",
        "image": "flutter-vs-react-native.png",
        "url": "https://dspace.uii.ac.id/bitstream/handle/123456789/30276/15523164%20Zakki%20Abdurrahman%20Haris.pdf?sequence=1&isAllowed=y"
      }
    ]

    if(portfolio){
      var projects = portfolio.map(prt => {
        return <div key={prt.title} className="columns portfolio-item">
           <div className="item-wrap">
            <a href={prt.url} title={prt.title}>
               <img alt={prt.title} src={process.env.PUBLIC_URL + '/images/portfolio/'+ prt.image} />
               <div className="overlay">
                  <div className="portfolio-item-meta">
                 <h5>{prt.title}</h5>
                     <p>{prt.category}</p>
                  </div>
                </div>
              <div className="link-icon"><i className="fa fa-link"></i></div>
            </a>
          </div>
        </div>
      })
    }

    return (
      <section id="portfolio">

      <div className="row">

         <div className="twelve columns collapsed">

            <h1>Check Out Some of My Works.</h1>

            <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                {projects}
            </div>
          </div>
      </div>
   </section>
    );
  }
}

export default Portfolio;
