import React, { Component } from 'react';

class Testimonials extends Component {
  render() {

   const testimonials = [
      {
        "text": "Yang terbaik di antara kalian adalah mereka yang berakhlak paling mulia.",
        "user": "Muhammad shallallahu alaihi wasallam"
      },
      {
        "text": "Lihatlah orang-orang yang di bawahmu dalam urusan harta dunia, dan jangan sekali-kali melihat yang berada di atasmu, supaya kamu tidak meremehkan karunia Allah yang diberikan kepadamu.",
        "user": "Muhammad shallallahu alaihi wasallam"
      }
    ]

    if(testimonials){
      var testimonial = testimonials.map(testim =>{
        return  <li key={testim.user}>
            <blockquote>
               <p>{testim.text}</p>
               <cite>{testim.user}</cite>
            </blockquote>
         </li>
      })
    }

    return (
      <section id="testimonials">
      <div className="text-container">
         <div className="row">

            <div className="two columns header-col">
               <h1><span>Client Testimonials</span></h1>
            </div>

            <div className="ten columns flex-container">
                  <ul className="slides">
                      {testimonial}
                  </ul>
               </div>
            </div>
         </div>
   </section>
    );
  }
}

export default Testimonials;
